import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import Fade from "react-reveal/Fade";
import Weather from "../components/Weather";
import Footer from "../components/Footer";
import "./Home.css";

const API_KEY = "80e4e2378e235fae347d6f18a4538af9";
// const API_KEY = process.env.REACT_APP_WEATHER_KEY;

const Home = () => {

    const [state, setState] = useState({
        isLoading: true,
        condition: '',
        temp: '',
        city: ''
    });
    const loadedCoords = localStorage.getItem("cords");

    const GetWeather = async (lat, lng) => {
        const {
            data: {
                main: { temp },
                weather,
                name
            }
        } = await axios.get(`https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${lng}&appid=${API_KEY}&units=metric`);
        setState({
            isLoading: false,
            condition: weather[0].main,
            temp,
            city: name
        });
    }

    useEffect(() => {

        if (loadedCoords === null) {
            if (window.navigator.geolocation) { // GPSë¥¼ ì§€ì›í•˜ë©´
                window.navigator.geolocation.getCurrentPosition((position) => {
                    const latitude = position.coords.latitude;
                    const longitude = position.coords.longitude;
                    const coordsObj = {
                        latitude,
                        longitude
                    };
                    localStorage.setItem("cords", JSON.stringify(coordsObj));
                    window.location.reload();
                });
            } else {
                alert("GPSë¥¼ ì§€ì›í•˜ì§€ ì•ŠìŠµë‹ˆë‹¤.");
            }
        } else {
            const parseCoords = JSON.parse(loadedCoords);
            GetWeather(parseCoords.latitude, parseCoords.longitude);
        }

    }, []);

    return (
        <div className="container">
            {loadedCoords === null ?
                // nullì´ë©´
                <div className="loader">
                    <h2>ìœ„ì¹˜í™•ì¸ í—ˆìš©ì„ ëˆŒëŸ¬ì£¼ì„¸ìš”.</h2>
                </div>
                : // nullì´ ì•„ë‹ˆë©´
                <Fade>
                    <div className="home_container">
                        <section className="home_title">
                            <h1>WHAT TO EAT TODAY?</h1>
                            <h2>ì˜¤ëŠ˜ ë­ ë¨¹ì§€?</h2>
                            {/* <img src={logoImage} className="home_logo" alt="WHAT TO EAT TODAT? ì˜¤ëŠ˜ ë­ë¨¹ì§€?"/> */}
                        </section>
                        <section className="home_weather">
                            {state.isLoading ?
                                <p className="home_loading">loading..</p> :
                                <Fade>
                                    <Weather
                                        temp={state.temp}
                                        condition={state.condition}
                                        city={state.city} />
                                </Fade>
                            }
                        </section>
                        <section className="home_start">
                            <Link to={{
                                pathname: "/ask",
                                state: {
                                    temp: state.temp,
                                    condition: state.condition
                                }
                            }}>
                                <button className="btn">START</button>
                            </Link>
                        </section>
                    </div>
                </Fade>
            }
            <Footer />
        </div >
    );
}

export default Home;